* {
    margin: 0;
    padding: 0;
    outline: none;
    box-sizing: border-box;
}


.todoLists {
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}

.todoList {
    width: 300px;
    min-height: 300px;
    margin: 50px auto;
    border-radius: 20px;
    border: solid 8px #2c3033;
    box-shadow: 0 0 0px 1px rgba(255, 255, 255, .4),
    0 0 0px 3px #2c3033;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    background-color: #bdcdd6;
}

.todoList-header__title {
    margin-left: 20px;
}

.todoList-header__delete-button {
    margin-right: 20px;
}
.filter-active {
    background-color: rgba(171, 218, 245, 0.51);
}

.taskChecked {
    opacity: 0.4
}



input[type=text] {
    flex: 1;
    font-size: 16px;
    padding: 10px 20px;
    border: 0;
    box-shadow: 0 -1px 0 #e2e4ea inset;
    color: #455963;
    width: 200px
}

input[type=text]:focus {
    box-shadow: 0 -1px 0 #bdcdd6 inset;
}


input[type=text].inputErrorStyle {
    box-shadow: 0 -1px 0 red inset;
}

.todoList-task {
    display: flex;
    justify-content: space-between;
    align-items: baseline;
    padding: 10px;

}

.todoList-task:nth-child(odd) {
    background: rgba(0, 0, 50, .1);
}


.todoList-headerWrapper {
    display: flex;
    justify-content: space-between;
    align-items: center;
    width: 100%;
    height: 50px;
    border-radius: 10px 10px 0 0px;
    background-image: linear-gradient(#687480 0%, #3b4755 100%);
    font: bold 18px/50px arial;
    text-align: center;
    color: white;
}

.todoList-header__title {
    margin-right: 10px;
}

.todoList-header .todoList-newTaskForm {
    display: flex;
    margin: 10px;
}

.todoList-header .todoList-newTaskForm button {
    padding: 10px;
}

.todoList__wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 100%;

}

.todoList-footer {
    padding-bottom: 10px;
    padding-left: 10px;
}